import { useState } from "react";
"use client";
import { useState } from "react";

export default function Support() {
    // track which images failed to load so we can show a lucide fallback
    const [imgError, setImgError] = useState<Record<number, boolean>>({});

    const supportData = [
        {
            src: '/images/supports/free-delivery.svg',
            title: 'Free Delivery',
            text: 'From all order over $500',
        },
        {
            src: '/images/supports/support-24-7.svg',
            title: 'Support 24/7',
            text: 'Shop with an expert',
        },
        {
            src: '/images/supports/gift-voucher.svg',
            title: 'Gift Voucher',
            text: 'Refer a friend',
        },
        {
            src: '/images/supports/secure-payment.svg',
            title: 'Secure payment',
            text: '100% Protected',
        }
    ];

    return (
        <div className="flex gap-5 justify-center py-12 sm:py-16 max-w-[882px] mx-auto">
            {supportData.map((item, index) => {
                return (
                    <div key={index} className="flex items-center text-center py-2 px-4 border border-[#BBBBBB] rounded-[10px] gap-2 flex-1 justify-center">
                        {/* try to load image from public/images/supports; fall back to lucide icon on error */}
                        {!imgError[index] && (
                            <img
                                src={item.src}
                                alt={item.title}
                                className="w-8 h-8 object-contain"
                                onError={() => setImgError(prev => ({ ...prev, [index]: true }))}
                            />
                        )}
                        <div className="flex flex-col items-baseline">
                            <h5 className="font-semibold text-[#1D1D1D] text-base">{item.title}</h5>
                            <p className="text-xs text-[#878787]">{item.text}</p>
                        </div>
                    </div>
                );
            })}
        </div>
    );
}
